import React, { Component } from "react";
import Input from './inputs';
const Form =(props) => {



    return (
      <div>
        <p>Imię: {props.name}</p>
        <p>Nazwisko: {props.surname}</p>
        {/* Zmiana inputa! sprawdzamy */}
        <Input name = {props.name} handleChange={props.updName} text ="Podaj imię:"/>
        <Input name = {props.surname} handleChange={props.updSurname} text ="Podaj nazwisko"/>
        <Input name = {props.startPlace} handleChange={props.updStartPlace} text ="Podaj miejsce rozpoczęcia"/>
        <Input name = {props.endPlace} handleChange={props.updEndPlace} text ="Podaj miejsce zakończenia"/>
        <Input name = {props.destination} handleChange={props.updDestitation} text ="Podaj cel podróży"/>
        <Input name = {props.travelID} handleChange={props.updTransport} text ="Podaj środek transportu"/>
        <Input name = {props.transport} handleChange={props.updTravelID} text ="Podaj nr delegacji"/>
      </div>
    );
  
}
export default Form;
