import React, { Component } from "react";



export default class Expense extends Component {
    constructor(props)
    {
        super(props);
        this.state = {
            amount:"",
            type:"",
        }
    }
    handleChange = (event)=>{
        let name = event.target.name;
        var cc = event.target.value;
        this.setState({[name]:cc})
        console.log("Dokonało się", this.state.amount);
        
    }
  render() {
    return (
      <div>
        <select>
          <option value="0">...</option>
          <option value="1">Hotel</option>
          <option value="2">Inne</option>
          <option value="3">Kolej</option>
          <option value="4">Nocleg</option>
          <option value="5">Samochód prywatny do 900cm^2</option>
          <option value="6">Samochód prywatny powyżej 900cm^2</option>
          <option value="7">Samochód służbowy</option>
          <option value="8">Samolot</option>
          <option value="9">Taxi</option>
          <option value="10">Zaliczka</option>
        </select>
        <input
          type="number"
          placeholder="Tutaj sprawdzamy"
          name="amount"
          onChange={this.handleChange}
        />
        <h1>{this.state.amount}</h1>
        
        
        <div>Wybór waluty????</div>
        <h1>Ile wyszlo za ten wydatek</h1>
        <select>
          <option value="employee">Pracownik</option>
          <option value="employer">Karta firmowa</option>
        </select>
      </div>
    );
  }
}
