import React, { Component } from "react";
import InputDate from "../Components/inputDate";
import { getAllCountries } from "../Services/travelCosts";
import Option from "../Components/selects";
import { allNBPCurrency } from "../Services/servicurrency";
import Form from "../Components/Form";
import Expense from './expenditure';
export default class Highorder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      allCountries: [],
      selectValue: "eur",
      startDate: "",
      stopDate: "",
      bid: 0,
      diet: 0,
      accommodation: 0,
      country: {},
      countryData: {},
      currency: "",
      traveler: {
        name: "Jan",
        surname: "Nowak",
      },
      startPlace: "",
      destination: "",
      endPlace: "",
      travelID: "",
      desctription: "",
      transport: "",
    };
  }

  updateName = (event) => {
    this.setState({
      traveler: {
        name: event.target.value,
        surname: this.state.traveler.surname,
      },
    });
  };
  updateSurname = (event) => {
    this.setState({
      traveler: { surname: event.target.value, name: this.state.traveler.name },
    });
  };
  updateStartPlace = (event) => {
    this.setState({
      startPlace: event.target.value,
    });
  };
  updateEndPlace = (event) => {
    this.setState({
      endPlace: event.target.value,
    });
  };
  updateDestination = (event) => {
    this.setState({
      destination: event.target.value,
    });
  };
  updatetravelID = (event) => {
    this.setState({
      travelID: event.target.value,
    });
  };
  updateTransport = (event) => {
    this.setState({
      transport: event.target.value,
    });
  };

  componentDidMount() {
    getAllCountries().then((country) => {
      this.setState({ allCountries: country });
      console.log(this.state.allCountries);
    });
    allNBPCurrency(this.state.selectValue).then((element) => {
      console.log("Currency get from BNP", element);
      this.setState({ bid: element.rates[0].bid });
    });
  }

  updateText = () => {
    console.log("Value state currency", this.state.selectValue);
  };

  updateText1 = () => {
    console.log("Calendar value:", this.state.startDate);
  };
  // Zmiana w dropdown liscie
  handleChange = (event) => {
    var cc = event.target;
    var test = new Promise(function (resolve, reject) {
      console.error("Sprawdzam teraz tablice wszystkich krajw");
      resolve("First promise done");
    });
    test
      .then((result) => {
        console.log("A co to jest?: ", result);
        const foundCountry = this.state.allCountries.find((element) => {
          if (cc.value === element.code) {
            console.log("Znalazlem element w tablicy!", element);
            return element;
          }
        });
        this.setState({ country: foundCountry });
        return foundCountry;
      })
      .then((result) => {
        console.log("Atutaj w drugim then: ", result);
        allNBPCurrency(result.currency).then((element) => {
          console.log("Data from NBP:", element);
          this.setState({
            bid: element.rates[0].bid,
            currency: result.currency,
          });
        });
      });
  };

  handleInputStartDate = (event) => {
    this.setState({ startDate: event.target.value });
  };
  render() {
    if (this.state.allCountries.length === 0) {
      return <div>Lol pusto teraz</div>;
    } else {
      const CountiesSelect = this.state.allCountries.map((element) => {
        return <Option code={element.code} country={element.country} />;
      });
      return (
        <div className="container-contact">
          Krajowy<InputDate handleChange={this.handleInputStartDate} />
          Krajowy<InputDate />

          <select id="selColor" onChange={this.handleChange}>
            {CountiesSelect}
            {/* <option value="nothing-selected" selected="selected">Choose</option> */}
          </select>
          {/* <button onClick={this.updateText}>Sprawdz wartosc okej??</button>
          <button onClick={this.updateText1}>Sprawdz mi cene</button>
          <button onClick={this.promiseTest}>Sprawdz promise</button>
          <button onClick={this.promiseTest}>Poka wartosci kraju :D</button> */}

          <Form
            name={this.state.traveler.name}
            surname={this.state.traveler.surname}
            updName={this.updateName}
            updSurname={this.updateSurname}
            startPlace={this.state.startPlace}
            endPlace={this.state.endPlace}
            destination={this.state.destination}
            travelID={this.state.travelID}
            transport={this.state.transport}
            updStartPlace={this.updateStartPlace}
            updEndPlace={this.updateEndPlace}
            updDestitation={this.updateDestination}
            updTransport={this.updatetravelID}
            updTravelID={this.updateTransport}
          />
            <Expense/>
          <p>{this.state.bid}</p>
          <p>{this.state.currency}</p>
          <p>Kraj to: {this.state.country.country}</p>
        </div>
      );
    }
  }
}
